'use client';
import React from 'react'
import toast from 'react-hot-toast';

const DashboardHeader = ({id, firstName, lastName}) => {

    const handleLogout = async () => {
        try {
            const logoutResponse = await fetch("http://localhost:3100/api/v1/auth/logout", {
                method: "POST",
                credentials: "include",
                headers: {
                    "Content-Type": "application/json"
                }
            });

            const logoutResponseData = await logoutResponse.json();

            if (logoutResponse.status === 200) {
                console.log(logoutResponseData.message);
            } else {
                console.log(logoutResponseData.message);
            }
        } catch (error) {
            console.error("Fetch error:", error);
        }
    }

  return (
    <div className='w-[600px] h-32 bg-white/20 backdrop-blur-3xl rounded-lg shadow-md fixed top-10 flex justify-between items-center px-4'>
        <div className='flex gap-1 grow'>
            <h2 className='text-xl font-bold'>{firstName}</h2>
            <h2 className='text-xl font-bold'>{lastName}</h2>
        </div>
        <div className='flex gap-4'>
            <button className='text-white grow bg-green-600 hover:bg-green-700 transition-all rounded-md px-4 py-2'>Add Todo</button>
            <button onClick={handleLogout} className='text-white grow bg-red-600 hover:bg-red-700 transition-all rounded-md px-4 py-2'>Log Out</button>
        </div>
        {/* <div className='absolute top-0 left-0 h-full w-full rounded-lg bg-black/50'></div> */}
    </div>
  )
}

export default DashboardHeader